.event-box {
    display: inline-block;
    width: 309px;
    height: 180px;
    background-image: url('../img/default1.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}
.event-title {
    font-size: 20px;
    font-weight: 600;
    text-shadow: 1px 1px 0 rgba(0, 0, 0, .26);
    color: #fff;
    text-align: left;
    width: 90%;
    height: 80px;
    overflow-y: hidden;
    margin: 100px 0px 0px 5%;
}